.btn {
  @include set-Roboto-Condensed-Bold();
  border: 1px solid #ffffff;
  box-sizing: border-box;
  border-radius: 5px;
  font-style: normal;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  background-color: transparent;
  outline: none;

  &:hover {
    cursor: pointer;
  }

  &:focus {
    opacity: 0.5;
  }
}
